:root {
  /* Colors */
  --primary: #059669; /* Emerald 600 */
  --primary-hover: #047857; /* Emerald 700 */
  --secondary: #10b981; /* Emerald 500 */
  --accent: #d1fae5; /* Emerald 100 */
  --dark: #1f2937; /* Gray 800 */
  --light: #f3f4f6; /* Gray 100 */
  --white: #ffffff;
  --text-main: #374151; /* Gray 700 */
  --text-light: #6b7280; /* Gray 500 */

  /* Spacing */
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 2rem;
  --spacing-xl: 4rem;

  /* Fonts */
  --font-main: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif;

  /* Layout */
  --max-width: 1200px;
  --header-height: 70px;
  --radius: 8px;
  --shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-main);
  background-color: var(--light);
  color: var(--text-main);
  line-height: 1.6;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

/* -- Utilities -- */
.container {
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 0 var(--spacing-md);
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
}

.btn-primary {
  background-color: var(--primary);
  color: var(--white);
}

.btn-primary:hover {
  background-color: var(--primary-hover);
}

.btn-outline {
  border: 2px solid var(--primary);
  color: var(--primary);
  background: transparent;
}

.btn-outline:hover {
  background-color: var(--accent);
}

.full-width {
  width: 100%;
}

/* -- Navbar -- */
.navbar {
  height: var(--header-height);
  background: var(--white);
  box-shadow: var(--shadow);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar-container {
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 0 var(--spacing-md);
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-logo {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--primary);
}

.nav-menu {
  display: flex;
  gap: var(--spacing-lg);
  align-items: center;
}

.nav-item {
  color: var(--text-main);
  font-weight: 500;
}

.nav-item:hover,
.nav-item.active {
  color: var(--primary);
}

.nav-button {
  background: var(--primary);
  color: var(--white);
  padding: 0.5rem 1rem;
  border-radius: var(--radius);
}

.nav-button:hover {
  background: var(--primary-hover);
  color: var(--white);
}

.mobile-menu-icon {
  display: none;
  cursor: pointer;
}

.mobile-nav {
  display: none;
  background: var(--white);
  border-top: 1px solid var(--light);
}

/* -- Page Layout -- */
.page {
  min-height: calc(100vh - var(--header-height));
  width: 100%;
}

/* -- Home Page -- */
.home-page .hero {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("https://images.unsplash.com/photo-1487754180451-c456f719a1fc?auto=format&fit=crop&w=1600&q=80")
      center/cover;
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: var(--white);
  padding: var(--spacing-md);
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: var(--spacing-md);
}

.hero-content p {
  font-size: 1.25rem;
  margin-bottom: var(--spacing-lg);
  opacity: 0.9;
}

.hero-buttons {
  display: flex;
  gap: var(--spacing-md);
  justify-content: center;
}

.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-lg);
  max-width: var(--max-width);
  margin: var(--spacing-xl) auto;
  padding: 0 var(--spacing-md);
}

.feature-card {
  background: var(--white);
  padding: var(--spacing-lg);
  border-radius: var(--radius);
  text-align: center;
  box-shadow: var(--shadow);
}

.feature-icon {
  color: var(--primary);
  margin-bottom: var(--spacing-md);
}

.about-preview {
  background: var(--white);
  padding: var(--spacing-xl) 0;
  text-align: center;
}

/* -- Pricing Page -- */
.pricing-page {
  padding: var(--spacing-xl) 0;
}

.pricing-page h1,
.contact-page h1,
.faq-page h1,
.book-now-page h1 {
  text-align: center;
  margin-bottom: var(--spacing-sm);
  color: var(--dark);
}

.subtitle {
  text-align: center;
  color: var(--text-light);
  margin-bottom: var(--spacing-xl);
}

.pricing-cards {
  display: flex;
  justify-content: center;
  gap: var(--spacing-lg);
  flex-wrap: wrap;
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 0 var(--spacing-md);
}

.pricing-card {
  background: var(--white);
  padding: var(--spacing-lg);
  border-radius: var(--radius);
  width: 350px;
  box-shadow: var(--shadow);
  border: 1px solid transparent;
  position: relative;
}

.pricing-card.featured {
  border-color: var(--primary);
  transform: scale(1.05);
}

.tag {
  position: absolute;
  top: -12px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--primary);
  color: var(--white);
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 600;
}

.price {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary);
  margin: var(--spacing-md) 0 0;
}

.price-desc {
  color: var(--text-light);
  margin-bottom: var(--spacing-lg);
}

.features-list li {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-sm);
  color: var(--text-main);
}

/* -- FAQ Page -- */
.faq-page {
  padding: var(--spacing-xl) 0;
}
.faq-list {
  max-width: 800px;
  margin: 0 auto;
}
.faq-item {
  background: var(--white);
  border-radius: var(--radius);
  margin-bottom: var(--spacing-md);
  box-shadow: var(--shadow);
  overflow: hidden;
}
.faq-question {
  padding: var(--spacing-md);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  cursor: pointer;
}
.faq-answer {
  padding: 0 var(--spacing-md) var(--spacing-md);
  color: var(--text-main);
  border-top: 1px solid var(--light);
}

/* -- Contact Page -- */
.contact-page {
  padding: var(--spacing-xl) 0;
}
.split-layout {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--spacing-xl);
  align-items: start;
}
.contact-method {
  display: flex;
  align-items: flex-start;
  gap: var(--spacing-md);
  margin-top: var(--spacing-lg);
}
.contact-method .icon {
  color: var(--primary);
}
.contact-form {
  background: var(--white);
  padding: var(--spacing-lg);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
}
.form-group {
  margin-bottom: var(--spacing-md);
}
.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
}
.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: var(--radius);
  font-family: inherit;
}
.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 4px var(--accent);
}

/* -- Book Now -- */
.book-now-page {
  padding: var(--spacing-xl) 0;
}
.booking-widget-placeholder {
  max-width: 800px;
  margin: 0 auto;
  background: var(--white);
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #d1d5db;
  border-radius: var(--radius);
  text-align: center;
}

/* -- Footer -- */
.footer {
  background: var(--dark);
  color: var(--white);
  padding: var(--spacing-xl) 0 var(--spacing-lg);
  margin-top: auto;
}
.footer-content {
  max-width: var(--max-width);
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--spacing-xl);
  padding: 0 var(--spacing-md) var(--spacing-lg);
}
.footer h3 {
  color: var(--primary);
  margin-bottom: var(--spacing-md);
}
.footer h4 {
  margin-bottom: var(--spacing-md);
  color: var(--white);
}
.footer ul li {
  margin-bottom: var(--spacing-sm);
}
.footer a {
  color: #9ca3af;
  transition: color 0.2s;
}
.footer a:hover {
  color: var(--white);
}
.footer-section.contact li {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  color: #9ca3af;
}
.footer-bottom {
  border-top: 1px solid #374151;
  text-align: center;
  padding-top: var(--spacing-lg);
  color: #9ca3af;
  font-size: 0.875rem;
}

/* -- Mobile -- */
@media (max-width: 768px) {
  .desktop-menu {
    display: none;
  }
  .mobile-menu-icon {
    display: block;
  }
  .mobile-nav.active {
    display: flex;
    flex-direction: column;
    padding: var(--spacing-md);
  }
  .mobile-nav-item {
    padding: var(--spacing-md) 0;
    border-bottom: 1px solid var(--light);
    color: var(--text-main);
  }
  .split-layout {
    grid-template-columns: 1fr;
  }
  .hero-content h1 {
    font-size: 2rem;
  }
}
